<template>
  <el-row>
    <el-col :span="24">
      <el-row type="flex" class="row-bg" justify="space-between">
        <el-col :span="24">
          <el-row type="flex" class="row-bg" justify="space-between">
            <el-col :span="12">
              <h3>响应替换配置</h3>
            </el-col>

          </el-row>
        </el-col>
      </el-row>
      <el-divider />
      <el-row class="text-align-right">
        <div class="domain-search-input">
          <el-input placeholder="请输入名称进行搜索" prefix-icon="el-icon-search" v-model="domainSearch"></el-input>
        </div>
        <el-button type="primary" @click="onClickCreateIdentity()">新增</el-button>
      </el-row>
      <div class="demo-block">
        <el-table :data="
          tableData.filter(
            (data) =>
              !domainSearch ||
              data.name.toLowerCase().includes(domainSearch.toLowerCase())
          )
        " style="width: 100%">
          <el-table-column prop="name" label="名称"></el-table-column>
          <el-table-column prop="detail" label="详情"></el-table-column>
          <el-table-column prop="status" label="响应头替换">
            <template #default="scope">
              <span v-if="scope.row.response_header_status == 'true'">
                <i class="el-icon-circle-check icon-success"></i>已开启
              </span>
              <span v-if="scope.row.response_header_status == 'false'">
                <i class="el-icon-circle-close icon-error"></i>已关闭
              </span>
            </template>
          </el-table-column>
          <el-table-column prop="status" label="响应体替换">
            <template #default="scope">
              <span v-if="scope.row.response_data_status == 'true'">
                <i class="el-icon-circle-check icon-success"></i>已开启
              </span>
              <span v-if="scope.row.response_data_status == 'false'">
                <i class="el-icon-circle-close icon-error"></i>已关闭
              </span>
            </template>
          </el-table-column>
          
          <el-table-column label="操作" align="right">
            <template #default="scope">
              <el-button size="mini" @click="handleEdit(scope.row)" class="button-block" type="text"
                :loading="scope.row.loading">编辑</el-button>

              <el-popover placement="top" width="160" v-model:visible="scope.row.isVisiblePopover">
                <p>确定删除吗？</p>
                <div style="text-align: right; margin: 0">
                  <el-button size="mini" type="text" @click="scope.row.isVisiblePopover = false">取消</el-button>
                  <el-button type="primary" size="mini" @click="handleDelete(scope.row)" :loading="loading">确定
                  </el-button>
                </div>
                <template #reference>
                  <el-button type="text" size="mini" @click="scope.row.isVisiblePopover = true">删除</el-button>
                </template>

              </el-popover>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-col>

    <el-dialog :title="domainTitle" v-model="dialogIdentityFormVisible" width="820px" :close-on-click-modal="false"
      @closed="dialogClose">
      <el-form class="form-tag-dialog identity-edit-form " :model="identityForm" size="mini" label-position="left"
        label-width="130px" :rules="rules" ref="identityForm">
        <el-form-item label="名称" prop="name" key="1">
          <el-input v-model="identityForm.name" v-if="domainType == 'new'" placeholder="请输入字母开头，只包含字母、数字、下划线“_”、中横线“-”">
          </el-input>
          <el-input v-model="identityForm.name" v-if="domainType == 'edit'" disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="详情" key="2">
          <el-input v-model="identityForm.detail" placeholder="请输入详情"></el-input>
        </el-form-item>

        <el-form-item label="响应头替换" key="9">
          <el-switch v-model="identityForm.response_header_status" active-value="true" inactive-value="false"></el-switch>
        </el-form-item>
        <div v-if="identityForm.response_header_status == 'true'">
          <el-form-item label="响应头替换配置" key="10">
            <div class="match-title">
              <p>Header头</p>
              <p>正则匹配</p>
              <p>替换内容</p>
            </div>
            <div class="match-box" v-for="(item, index) in ruleConfigMatchs" :key="'matchs' + index">
              <div class="match-key-item">
                <el-input v-model="item.key"> </el-input>
              </div>
              <div class="match-key-item">
                <el-input v-model="item.match"> </el-input>
              </div>
              <div class="match-key-item">
                <el-input v-model="item.data"> </el-input>
              </div>

              <el-button @click.prevent="removeConfigMatchs(item, bigIndex)">删除</el-button>
            </div>
            <el-button @click="addConfigMatchs(bigIndex)" plain type="primary" class="button-new">新增</el-button>
          </el-form-item>
        </div>


        <el-form-item label="响应体替换" key="6">
          <el-switch v-model="identityForm.response_data_status" active-value="true" inactive-value="false"></el-switch>
        </el-form-item>

        <div v-if="identityForm.response_data_status == 'true'">
          <el-form-item label="响应体匹配正则" key="7">
            <el-input v-model="identityForm.response_data_replace_match" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="响应体替换内容" key="8">
            <el-input v-model="identityForm.response_data_replace_data" placeholder="请输入"></el-input>
          </el-form-item>
        </div>

      </el-form>
      <template #footer class="dialog-footer">
        <el-button @click="dialogIdentityFormVisible = false">取消</el-button>
        <el-button type="primary" @click="onClickIdentitySubmit('identityForm')" :loading="loading">确定</el-button>
      </template>
    </el-dialog>
  </el-row>
</template>
<script>
import { validateRuleName, mixin, JXAjax } from "../assets/scripts/common";
export default {
  mixins: [mixin],
  data() {
    return {
      domainTitle: "新增",
      domainType: "new",
      domainSearch: "",
      dialogIdentityFormVisible: false,
      loading: false,
      identityForm: {
        name: "",
        detail: "",
        response_header_status: "false",
        response_header_replace_data: "",
        response_data_status: "false",
        response_data_replace_match: "",
        response_data_replace_data: "",
      },
      tableData: [],
      ruleConfigMatchs: [{
        key: "",
        match: "",
        data: "",
      }],
    };
  },
  computed: {
    rules() {
      return {
        name: [
          {
            required: true,
            message: '请输入字母开头，只包含字母、数字、下划线“_”、中横线“-”',
            trigger: ["blur", "change"],
          },
          {
            validator: validateRuleName,
            trigger: ["blur", "change"],
          },
        ],
        
      }
    }
  },

  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      var t = this;
      JXAjax(
        "get",
        "/waf/waf_get_sys_response_replace_list",
        {},
        function (response) {
          t.tableData = response.data.message;
          t.tableData.forEach(item => {
            item.isVisiblePopover = false;
          })
        },
        function () {

        }
      );
    },

    dialogClose() {
      this.identityForm = {
        name: "",
        detail: "",
        response_header_status: "false",
        response_header_replace_data: "",
        response_data_status: "false",
        response_data_replace_match: "",
        response_data_replace_data: "",
      };
      this.ruleConfigMatchs = [{
        key: "",
        match: "",
        data: "",
      }];
      this.$refs["identityForm"].resetFields();
    },


    onClickIdentitySubmit(identityForm) {
      var t = this;
      var postUrl = "/waf/waf_create_sys_response_replace";
      if (t.domainType == "edit") {
        postUrl = "/waf/waf_edit_sys_response_replace";
      }

      var response_header_replace_data = "";
      if (t.identityForm.response_header_status == 'true') {
        if (t.ruleConfigMatchs.length == 0) {
          t.$message({
            message: '请对自定义header头进行配置',
            type: "error",
          });
          return false;
        } else {
          for (var i in t.ruleConfigMatchs) {
            var item = t.ruleConfigMatchs[i]
            if (i == 0) {
              response_header_replace_data = response_header_replace_data + '"' + item.key + '":{"replace_match":"' + item.match + '","replace_data":"' + item.data + '"}';
            } else {
              response_header_replace_data = response_header_replace_data + ',"' + item.key + '":{"replace_match":"' + item.match + '","replace_data":"' + item.data + '"}';
            }
          }
          response_header_replace_data = "{" + response_header_replace_data + "}"
        }
      }

      t.identityForm.response_header_replace_data = response_header_replace_data;
      this.$refs[identityForm].validate((valid) => {
        if (valid) {
          t.loading = true;
          JXAjax(
            "post",
            postUrl,
            t.identityForm,
            function (response) {
              t.loading = false;
              t.dialogIdentityFormVisible = false;
              t.getData();
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },

    onClickCreateIdentity() {
      var t = this;
      t.domainTitle = "新增";
      t.domainType = "new";
      t.dialogIdentityFormVisible = true;
    },

    handleEdit(data) {
      var t = this;
      data.loading = true;
      JXAjax(
        "post",
        "/waf/waf_get_sys_response_replace",
        { name: data.name },
        function (response) {
          data.loading = false;
          var oGetData = response.data.message;
          t.identityForm.name = oGetData.name;
          t.identityForm.detail = oGetData.detail;
          
          t.identityForm.response_header_status = oGetData.response_header_status;
          t.identityForm.response_header_replace_data = oGetData.response_header_replace_data;

          t.identityForm.response_data_status = oGetData.response_data_status;
          t.identityForm.response_data_replace_match = oGetData.response_data_replace_match;
          t.identityForm.response_data_replace_data = oGetData.response_data_replace_data;

          if (t.identityForm.response_header_status == "true") {
            var header_value = JSON.parse(t.identityForm.response_header_replace_data)
            var box = []
            for (var key in header_value) {
              var match = header_value[key].replace_match
              var odata = header_value[key].replace_data
              box.push({
                "key": key,
                "match": match,
                "data": odata,
              })
            }
            t.ruleConfigMatchs = box;
          }


          t.domainTitle = '编辑';
          t.domainType = "edit";
          t.dialogIdentityFormVisible = true;
        },
        function () {
          data.loading = false;
        },
        "no-message"
      );
    },

    handleDelete(data) {
      var t = this;
      t.loading = true;
      JXAjax(
        "post",
        "/waf/waf_del_sys_response_replace",
        { name: data.name },
        function (response) {
          data.isVisiblePopover = false;
          t.loading = false;
          t.getData();
        },
        function () {
          t.loading = false;
        }
      );
    },

    removeConfigMatchs(item) {
      var index = this.ruleConfigMatchs.indexOf(item);
      if (index > 0) {
        this.ruleConfigMatchs.splice(index, 1);
      }
    },

    addConfigMatchs() {
      this.ruleConfigMatchs.push({ key: "", match: "",data:"" })
    },
  },
};
</script>
<style>
.el-button.button-block {
  display: block;
  margin-left: 0px;
  text-align: right;
  width: 100%;
}

.icon-success {
  color: #67c23a;
  margin-right: 5px;
}

.icon-error {
  color: #f56c6c;
  margin-right: 5px;
}

.icon-warning {
  color: #e6a23c;
  margin-right: 5px;
}

.domain-setting {
  float: right;
}

.domain-redirect {
  margin-top: 5px;
  margin-bottom: 0px !important;
}

.el-form-item.is-required .redirect-box .el-form-item__label:before {
  content: "";
}

.domain-search-input {
  display: block;
  float: left;
  text-align: left;
}

.domain-search-input .el-input {
  width: 100%;
}

.no-padding {
  padding: 0;
}

.node-detail span {
  font-size: 12px;
}

.domain-tabs {
  margin-bottom: 18px;
}

.identity-edit-form .el-button {
  margin-left: 10px;
}

.identity-edit-form .button-new {
  margin-left: 0px;
}



.identity-edit-form .match-box {
  width: calc(100% - 74px);
}


.identity-edit-form .match-box {
  display: inline-block;
  margin-bottom: 10px;
  margin-right: 10px;
}

.identity-edit-form .match-title {
  display: flex;
}

.identity-edit-form .match-title p {
  display: inline-block;
  margin: 0 55px;
}

.identity-edit-form .match-key-item {
  margin-right: 10px;
  position: relative;
  display: inline-block;
  width: calc(100% - 417px);
}
</style>
